import React, { Component } from 'react';
import "./Quiz.css";
import musicData from '../../data/musicData';
import AudioPlayer from '../Audio/AudioPlayer';
import plugImage from '../../assets/img/gave-over-img.jpg';

export default class Quiz extends Component {

  render() {

    const { page, random, win } = this.props;

    return (
      <div className="random-bird jumbotron rounded">
        <div className="random-bird__image-wrapper">
          <img className="bird-image"
            src={(win) ? musicData[page][random].image : plugImage}
            alt={(win) ? musicData[page][random].name : 'bird'} />
        </div>
        <div className="random-bird__data-wrapper">
          <ul className="list-group list-group-flush">
            <li className="list-group-item">
              <h3>{win ? musicData[page][random].name : '******'}</h3>
            </li>
            <li className="list-group-item">
              <AudioPlayer url={musicData[page][random].audio} />
            </li>
          </ul>
        </div>
      </div>

    );
  }
}