import './App.css';
import React, { Component } from 'react';
import Header from '../Header/Header';
import Quiz from '../Quiz/Quiz';
import BirdsList from '../BirdsList/BirdsList';
import Bird from '../Bird/Bird';
import GameOver from '../GameOver/GameOver';
import succesSound from '../../assets/audio/succes.mp3';
import errorSound from '../../assets/audio/error.mp3';

export default class App extends Component {
  state = {
    random: 0,
    page: 0,
    id: 0,
    select: false,
    win: false,
    score: 0,
    step: 0
  }

  componentDidMount() {
    this.setState({
      random: this.randomBird()
    });

    this.succesPlayer = document.getElementById('succesPlayer');
    this.errorPlayer = document.getElementById('errorPlayer');
  }

  randomBird = () => {
    return Math.floor(Math.random() * 5);
  }

  onBirdSelected = (id, event) => {
    event.persist();

    this.setState({
      id: id - 1,
      select: true
    });

    if (!event.target.classList.contains('success') && !event.target.classList.contains('error')) {
      this.setState((prevState) => ({
        step: prevState.step + 1
      }));
    }

    this.getWin(id);
    this.styleListItem(event);
  };

  getWin = (id) => {
    if (id - 1 !== this.state.random || this.state.win) {
      this.playAlarm(false);
      return;
    }

    this.playAlarm(true);
    this.setState((prevState) => ({
      score: prevState.score + 5 - this.state.step,
      win: true
    }));
  }

  styleListItem = (event) => {
    if (event._targetInst.key - 1 === this.state.random && !this.state.win) {
      event.target.classList.add('success')
    } else if (event._targetInst.key - 1 !== this.state.random && !this.state.win) {
      event.target.classList.add('error')
    }
  }

  playAlarm = (param) => {
    if (param) {
      this.succesPlayer.currentTime = 0;
      this.succesPlayer.play();
    } else {
      this.errorPlayer.currentTime = 0;
      this.errorPlayer.play();
    }
  }

  getNextPage = () => {
    if (!this.state.win) return;

    this.setState((prevState) => ({
      random: this.randomBird(),
      page: prevState.page + 1,
      select: false,
      win: false,
      step: 0,
    }));
  }

  newGame = () => {
    this.setState({
      random: this.randomBird(),
      page: 0,
      select: false,
      win: false,
      score: 0,
      step: 0,
    });
  }

  render() {
    console.log('state', this.state);
    if (this.state.page === 6) {
      return (
        <>
          <Header
            page={this.state.page}
            score={this.state.score} />
          <GameOver
            score={this.state.score}
            newGame={this.newGame}
          />
        </>
      )
    }

    return (
      <>
        <Header
          page={this.state.page}
          score={this.state.score} />
        <Quiz
          random={this.state.random}
          page={this.state.page}
          win={this.state.win} />
        <button
          className={this.state.win ? "btn btn-next" : "btn"}
          onClick={this.getNextPage}>
          Next Level
        </button>
        <div className="row mb2">
          <BirdsList
            onItemSelected={this.onBirdSelected}
            page={this.state.page} />
          <Bird
            id={this.state.id}
            page={this.state.page}
            select={this.state.select} />
        </div>
        <div>
          <audio id="succesPlayer" src={succesSound}></audio>
          <audio id="errorPlayer" src={errorSound}></audio>
        </div>
      </>
    )
  }
}