import './GameOver.css';
import React from 'react';

const GameOver = ({ score, newGame }) => {
  if (score === 30) {
    return (
      <div>
        <h1 className="title-congratulation">Поздравляем! Вы абсолютный победитель</h1>
        <iframe id='video-prize' src={'https://www.youtube.com/embed/VJxU12m6too'} frameBorder={'0'} allowFullScreen title={'video-prize'}></iframe>
        <button
          className="btn btn-next btn-game-over"
          onClick={newGame}>
          Попробовать еще раз!
      </button>
      </div>
    )
  }

  return (
    <div className="jumbotron game-over">
      <h1 className="display-3 text-center">Поздравляем!</h1>
      <p className="lead text-center">Вы прошли викторину и набрали {score} из 30 возможных баллов</p>
      <hr className="my-4" />
      <button
        className="btn btn-next btn-game-over"
        onClick={newGame}>
        Попробовать еще раз!
      </button>
    </div>
  );
}

export default GameOver;
