import './Bird.css';
import React, { Component } from 'react';
import musicData from '../../data/musicData';
import AudioPlayer from '../Audio/AudioPlayer';

export default class Bird extends Component {

  render() {
    const { page, id, select } = this.props;

    const styleItem = {
      display: select ? 'flex' : 'none'
    };

    const styleInstruction = {
      display: select ? 'none' : 'block'
    };

    return (
      <div className="col-md-6">
        <div className="bird-details card">
          <p className="instruction"
            style={styleInstruction} >
            <span>Please, listen the player and make your choise</span>
          </p>
          <div className="card-body"
            style={styleItem}>
            <div className="img-wrapper">
              <img className="bird-image"
                src={musicData[page][id].image} alt={musicData[page][id].name} />
            </div>
            <ul className="list-group list-group-flush">
              <li className="list-group-item">
                <h4>{musicData[page][id].name}</h4>
              </li>
              <li className="list-group-item">
                <span>{musicData[page][id].track}</span>
              </li>
              <li className="list-group-item">
                <AudioPlayer url={musicData[page][id].audio} />
              </li>
            </ul>
          </div>
          <span className="bird-description"
            style={styleItem}>
            {musicData[page][id].description}
          </span>
        </div>
      </div>
    )
  }
}
