import React, { Component } from 'react';
import './Header.css';

export default class Header extends Component {

  componentDidUpdate() {
    const li = document.querySelectorAll('li');
    li.forEach((el, index) => {
      el.classList.remove('active');
      if (index === this.props.page) {
        el.classList.add('active');
      }
    })
  }

  render() {
    return (
      <div className="header d-flex">
        <div className="top-panel d-flex">
          <div className="logo">game-over&nbsp;<span className="logo-highlight">quiz</span></div>
          <h5>Score: <span className="score">{this.props.score}</span></h5>
        </div>
        <ul className="pagination">
          <li className="page-item active"><a className="page-link" href="/#">True 8 bit</a></li>
          <li className="page-item"><a className="page-link" href="/#">Classic</a></li>
          <li className="page-item"><a className="page-link" href="/#">Second half of the '90s</a></li>
          <li className="page-item"><a className="page-link" href="/#">Soundtracks</a></li>
          <li className="page-item"><a className="page-link" href="/#">Soundtracks 2</a></li>
          <li className="page-item"><a className="page-link" href="/#">Random</a></li>
        </ul>
      </div>
    );
  }
};